.background-challenges{
    position: relative;
}

.background-challenges div {
    position: relative;
    z-index: 2;
}

.background-challenges button {
    position: relative;
    z-index: 2;
}

.background-challenges:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background: rgba(0, 0, 0, 0.7);
    background-blend-mode: darken;
    background-image: url(/FootballTopScore/static/media/ajaxBack3.1c0c6e93.jpg);
    background-repeat: no-repeat;
    background-position: 50% 0;
    background-size: cover;
}
